/*
  Sensible defaults so that unstyled Studio would still show.
  Override them for your app.
*/

.eegStudio {
  font-size: 12px;
  line-height: 13px;
}

.ruler {
  height: 20px;
}

.viewport {
  height: 500px;
}

.channelsList {
  top: 20px; /* Same as ruler height */
}

.ctrlClickCatcher {
  top: 20px; /* Same as ruler height */
}

.annotation.defaultAnnotation {
  background-color: green;
  opacity: 0.3;
}

.annotationsMinimap {
  height: 100px;
}

.annotationMini.defaultAnnotation {
  background-color: green;
  opacity: 0.3;
}

.selectedAnnotation {
  border-color: cyan;
}
.leftBorder, .rightBorder {
  background-color: cyan;
}
.annotationLabel {
  color: cyan;
}

/*
  End of defaults.
*/

.eegStudio {
  position: relative;
  user-select: none;
}

/* Scroller */
.scroller {
}

.scroller.scroller__native {
  overflow-x: auto;
}

.scroller_container,
.scroller.scroller__transform {
  overflow-x: hidden;
}

/* Ruler */
.ruler {
  display: flex;
  position: relative;
}

.segment {
  display: flex;
  justify-content: space-between;
  position: absolute;
}

.second {
  width: 100%;
  display: flex;
  flex-direction: column;
  border-left: 1px solid #000;
  box-sizing: border-box;
}

.second_line_group {
  display: flex;
  justify-content: space-between;
}

.second_line {
  width: 1px;
  height: 7px;
  background-color: #000;
}

.second_line:first-child,
.second_line:last-child {
  width: 0;
}

.viewport {
  position: relative;
}

.plot {
  position: relative;
}
.plotSegment {
  position: absolute;
  pointer-events: none;
}

.plotCanvas {
  display: block;
}

.channelsList {
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  user-select: none;
}

.annotations {
  position: absolute;
  height: 100%;
}
.annotation {
  position: absolute;
  height: 100%;
  min-width: 1px;
}

.selectedAnnotation {
  position: absolute;
  height: 100%;
  border-top-width: 2px;
  border-bottom-width: 2px;
  border-top-style: solid;
  border-bottom-style: solid;
  outline: none; /* Has `tabindex` attribute */
  box-sizing: border-box;
}
.leftBorder, .rightBorder {
  position: absolute;
  
  height: 100%;
  width: 2px;
}
.leftBorder {
  left: 0;
  cursor: w-resize;
}
.leftBorder::after, .rightBorder::after {
  position: relative;
  z-index: 1; /* Overlap .measureLine */
  top: calc(50% + 9px - 5px); /* Same 9px as .measureLine relative offset; 5px is half own height */
  left: -5px; /* Half own width */

  display: block;
  content: " ";
  width: 10px;
  height: 10px;
  border: 1px solid black;
  background-color: white;
}
.leftBorder::before {
  position: absolute;
  top: -9px; /* 2px border + 7px size */
  left: 0;

  display: block;
  content: " ";
  width: 0;
  height: 0;

  border-style: solid;
  border-width: 7px 0 0 7px;
  border-color: transparent transparent transparent #ff5754;
}
.cursorW, .cursorW .rightBorder {
  cursor: w-resize;
}
.rightBorder {
  right: 0;
  cursor: e-resize;
}
.rightBorder::before {
  position: absolute;
  top: -9px; /* 2px border + 7px size */
  right: 0;

  display: block;
  content: " ";
  width: 0;
  height: 0;

  border-style: solid;
  border-width: 0 0 7px 7px;
  border-color: transparent transparent #ff5754 transparent;
}
.cursorE, .cursorE .leftBorder {
  cursor: e-resize;
}
.annotationDuration {
  position: relative;
  top: 50%;

  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.measureLine {
  position: relative;
  width: 100%;
  top: 9px;
  border-bottom: 1px solid #ff5754;
}
.leftGapper {
  flex-basis: 1px; /* Something minimal, just to have some width */
  flex-shrink: 0;
  height: 18px; /* For pushing content down */
}
.durationLabel {
  position: relative;
  background-color: white;
  margin-left: 7px; /* Set this to at least half size of .leftBorder::after element (it's 10px-wide in our example, plus border) */
  margin-right: 7px; /* Ditto for .rightBorder::after */
}
.annotationLabel {
  position: absolute;
  bottom: 0;
  right: 0;
  transform-origin: bottom right;
  transform: rotate(-90deg) translateX(100%);
  background-color: white;
}

.annotationsMinimap {
  position: relative;
}
.annotationMini {
  position: absolute;
  height: 100%;
  min-width: 1px; /* otherwise, many short annotations are completely invisible on minimap */
  pointer-events: none;
}
.cursorMinimap {
  position: absolute;
  height: 100%;
  width: 1px;
  background-color: red;
}

.ctrlClickCatcher {
  position: absolute;
  width: 100%;
  cursor: copy;
}
.newSelection {
  position: absolute;
  height: 100%;
  pointer-events: none;
  background-color: rgba(0, 0, 0, 0.1);
}


/* Cursor */
.cursor {
  position: absolute;
  top: 0;

  width: 1px;
  height: 100%;
  background-color: #000;
  pointer-events: none;
}

.cursorTime {
  white-space: nowrap;
  background-color: #fff;
}